body {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-icon{position:relative;cursor:pointer}.card-icon .shopping-logo{height:25px}.card-icon .item-count{position:absolute;top:0.3rem;left:0.5rem}

.card-dropdown{width:250px;height:350px;position:absolute;background:lightgray;z-index:100;top:4rem;right:1rem;padding:0.5rem}.card-dropdown .card-item{height:80%;display:flex;flex-direction:column;overflow:scroll}

.cart-item{width:100%;height:80px;display:flex;align-items:center;margin-bottom:15px}.cart-item img{width:30%;height:100%;border-radius:100%}.cart-item .details{width:70%;display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.cart-item .close-logo{padding-right:1rem;cursor:pointer}

.custom-button{border:none;padding:1rem 3rem;display:inline-block;border-radius:100px;font-size:1rem;font-weight:400;color:#ffffff;background:#000;margin-bottom:2rem;position:relative;-webkit-transition:all 0.2s;transition:all 0.2s;cursor:pointer;outline:none}.custom-button::after{content:'';width:100%;height:100%;background:#000;display:inline-block;position:absolute;top:0;left:0;border-radius:100px;z-index:-1;-webkit-transition:all 0.4s;transition:all 0.4s}.custom-button:hover{-webkit-transform:translateY(-3px);transform:translateY(-3px);box-shadow:0 10px 20px rgba(0,0,0,0.2);background:#ffffff;color:#000}.custom-button:hover::after{-webkit-transform:scaleX(1.4) scaleY(1.6);transform:scaleX(1.4) scaleY(1.6);opacity:0}

.header{width:100%;height:10vh;position:fixed;display:flex;justify-content:space-between;align-items:center;z-index:1000;background:#ece6e6}.header .logo{width:50%;display:flex;justify-content:flex-start;align-items:center;text-decoration:none}.header .brand{font-size:1.3rem;margin-left:10px;font-weight:800;color:purple}.header .logo-img{width:40px;height:40px;padding-left:30px}.header .options{width:50%;display:flex;justify-content:space-around;align-items:center}.header .options .option{width:100px;height:25px;text-decoration:none;padding:0.2rem 0.5rem;color:#ffffff;background:rgba(0,0,0,0.685);font-weight:600;-webkit-transition:all 1s;transition:all 1s;position:relative;border:1px solid black;-webkit-transition:all 0.4s;transition:all 0.4s;overflow:hidden;cursor:pointer}.header .options .option::after{content:'';width:100%;height:100%;background:linear-gradient(120deg, transparent, #e0dddd);display:inline-block;position:absolute;-webkit-transition:all 0.4s;transition:all 0.4s;-webkit-transform:translateX(28px);transform:translateX(28px);top:0}.header .options .option:hover::after{-webkit-transform:translateX(-220px);transform:translateX(-220px)}

.menu-item{position:relative;padding:1.5rem;top:6rem;min-width:75%;height:300px;background:#f3f2f2e1;display:flex;align-items:center;margin:0 7.5px 15px}.menu-item .wrapper{width:50%;height:90%;position:absolute;border:1px solid black;margin:10px 15px;overflow:hidden}.menu-item .wrapper:hover{cursor:pointer}.menu-item .wrapper:hover .background-image{-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:-webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:-webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95), -webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item .wrapper:hover .content{opacity:0.9}.menu-item .background-image{display:flex;justify-content:center;align-items:center;width:100%;height:100%;background-position:center;background-size:cover}.menu-item .left{left:10px}.menu-item .right{right:10px}.menu-item .content{font-family:'Bangers', cursive;position:absolute;color:white;padding:10px;background:#4f525291}.menu-item .content .title{letter-spacing:5px;-webkit-background-clip:text;background:transparent;color:#fff}.menu-item .text-body{width:40%;height:100%;position:absolute;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.menu-item .text-body .details{margin-top:1rem}.menu-item .text-body .btn{width:150px;height:50px;border:none;padding:1rem 2rem;display:inline-block;border-radius:100px;font-size:1rem;font-weight:400;color:#ffffff;background:#000;margin-bottom:2rem;position:relative;-webkit-transition:all 0.2s;transition:all 0.2s}.menu-item .text-body .btn::after{content:'';width:100%;height:100%;background:#000;display:inline-block;position:absolute;top:0;left:0;border-radius:100px;z-index:-1;-webkit-transition:all 0.4s;transition:all 0.4s}.menu-item .text-body .btn:hover{-webkit-transform:translateY(-3px);transform:translateY(-3px);box-shadow:0 10px 20px rgba(0,0,0,0.2);background:#ffffff;color:#000}.menu-item .text-body .btn:hover::after{-webkit-transform:scaleX(1.4) scaleY(1.6);transform:scaleX(1.4) scaleY(1.6);opacity:0}.ReactModalPortal>div{opacity:0}.ReactModal__Overlay{-webkit-transition:opacity 500ms ease-in-out;transition:opacity 500ms ease-in-out}.ReactModalPortal .ReactModal__Overlay--after-open{opacity:1}.ReactModalPortal .ReactModal__Overlay--before-close{opacity:0}.modal{width:500px;height:400px;background:lightgray;color:orange;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);display:flex;justify-content:space-around;align-items:center;flex-direction:column;word-break:break-all;padding:2rem;outline:none}

.home-page{height:300vh;padding:2rem;display:flex;position:relative;flex-direction:column;justify-content:center;align-items:center;background:linear-gradient(120deg, #499cf1, #f6f6f6, #e64969)}.home-page .heading{position:absolute;top:5%}.home-page .heading .title{font-size:1.7rem;font-family:'Courgette', cursive;letter-spacing:3px}

.collection-overview{width:100%;height:100%}

.collection{position:relative;top:5rem}.collection-menu{display:flex;flex-wrap:wrap;justify-content:center}.title{font-family:'ZCOOL XiaoWei';letter-spacing:2px}

.item-menu{width:40%;height:200px;display:flex;margin:2rem 0.5rem;background:#f5f3f3;font-family:'Oxanium'}.item-menu .image-container{width:50%}.item-menu .image-container .image{width:100%;height:100%;background-size:cover}.item-menu .details{width:50%;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.item-menu .details .custom-button{padding:1rem 2rem;opacity:0;display:none;-webkit-transition:all 0.2s;transition:all 0.2s}.item-menu .details:hover .custom-button{opacity:1;display:block}

.collection-page{width:100%;height:100%;position:relative;top:5rem}.collection-page .items{display:flex;align-items:center;justify-content:center;flex-wrap:wrap}

.footer{width:100%;height:40vh;background:rgba(0,0,0,0.6);color:white;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.footer .logo{width:70px;height:70px;position:relative;top:1rem}.footer .icons{width:40%;display:flex;justify-content:center;align-items:center}.footer li{width:20px;height:20px;list-style:none;margin:0 0 0 10px;padding:0.5rem;border-radius:50%;cursor:pointer}.footer .facebook{background:#1877f2}.footer .facebook:hover{box-shadow:5px 5px 10px 5px #1877f2}.footer .twitter{background:#1da1f2}.footer .twitter:hover{box-shadow:5px 5px 10px 5px #1da1f2}.footer .instagram{background:#d260ba}.footer .instagram:hover{box-shadow:5px 5px 10px 5px #d260ba}.footer .youtube{background:#ff0000}.footer .youtube:hover{box-shadow:5px 5px 10px 5px #ff0000}.footer .google{background:#7d7d7d}.footer .google:hover{box-shadow:5px 5px 10px 5px #7d7d7d}.footer span{margin:0 0 0 20px;font-size:12px;cursor:pointer;font-weight:500}.footer span:hover{font-size:15px;text-shadow:5px 5px 8px #ffffff}

.sign-in-sign-out{width:100%;height:100vh;display:flex;justify-content:space-around}

.sign-in{position:relative;top:20%;width:40%;height:400px;display:flex;flex-direction:column;align-items:center;background:-webkit-gradient(linear, left top, left bottom, from(#61a8f1), to(#e75572));background:linear-gradient(#61a8f1, #e75572);padding:20px 0;border-radius:10px}.sign-in .title{margin:10px 0}.sign-in .form{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.sign-in .button{width:100%;display:flex;justify-content:space-around}

.group{position:relative;margin:20px 0}.group .form-input{background:none;background-color:white;border:none;padding:10px 10px 10px 5px;color:gray;font-size:18px;border-bottom:1px solid gray;margin:25px 0;display:block}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:black}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{font-size:12px;color:gray;position:absolute;top:10px;left:5px;-webkit-transition:300ms ease all;transition:300ms ease all;font-weight:normal;pointer-events:none}.group .form-input-label.shrink{top:-14px;font-size:12px;color:black}

.sign-up{position:relative;top:20%;background:-webkit-gradient(linear, left top, left bottom, from(#61a8f1), to(#e75572));background:linear-gradient(#61a8f1, #e75572);width:40%;height:500px;display:flex;flex-direction:column;align-items:center;justify-content:space-around;border-radius:10px}

